<template>
  <div class="home">
    <span class="el-icon-user-solid">{{userInfo.username}}</span>
    <el-divider></el-divider>
    <span class="el-icon-setting">负 载 {{loadavg.one}} {{loadavg.five}} {{loadavg.fifteen}}</span>
    <el-divider></el-divider>
    <span class="el-icon-setting">CPU {{sysinfo.cputype}} 核心 {{sysinfo.cpunum}}</span>
    <el-divider></el-divider>
    <span class="el-icon-setting">根分区 总大小 {{diskinfo.dasize}} 使用率 {{diskinfo.dause}}</span>
  </div>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      timer: null,
      loadavg: {},
      sysinfo: {},
      diskinfo: {}
    };
  },
  methods: {
    getloadavg(info) {
      this.$http
        .gethome(info)
        .then(res => {
          if (res.errcode === 0) {
            if (info == "sysinfo") {
              this.sysinfo = res.data;
            } else if (info == "loadavg") {
              this.loadavg = res.data;
            } else if (info == "diskinfo") {
              this.diskinfo = res.data;
            }
          }
        })
        .catch(error => {
          console.log(error);
        });
      return;
    }
  },
  computed: {
    userInfo() {
      return this.$store.state.userInfo;
    }
  },
  created() {
    this.getloadavg("sysinfo");
    this.getloadavg("loadavg");
    this.getloadavg("diskinfo");
    this.timer = setInterval(() => {
      this.getloadavg("loadavg");
    }, 10000);
  },
  destroyed() {
    clearInterval(this.timer);
  },
  components: {}
};
</script>

<style>
.home {
  /* text-align: center; */
  padding: 30px;
  margin-bottom: 30px;
}
</style>



